<template>
  <div
    class="h-screen flex w-full bg-img vx-row no-gutter items-center justify-center"
    id="page-login"
  >
    <div class="vx-col sm:w-1/2 md:w-1/2 lg:w-3/4 xl:w-3/5 sm:m-0 m-4">
      <vx-card>
        <div slot="no-body" class="full-page-bg-color">
          <div class="vx-row no-gutter justify-center items-center">
            <div class="vx-col">
              <img
                src="@/assets/images/logo/logo.png"
                alt="login"
                class="mx-auto"
              />
            </div>

            <div class="vx-col w-full d-theme-dark-bg">
              <div class="p-8 login-tabs-container">
                <div class="vx-card__title mb-4">
                  <h4 class="mb-4">
                    List Firebase CMS
                  </h4>
                </div>

                <div>
                  <vs-list>
                    <vs-list-item
                      v-for="(app, index) in listFirebaseApp"
                      :key="app.projectId"
                      :title="app.projectId"
                      class="firebase-list-item"
                      @click.native="openApp(app)"
                    >
                      <template slot="avatar">
                        <img
                          width="30"
                          height="30"
                          alt="Google, firebase Free Icon of Google IO 2016"
                          class="m-4"
                          src="data:image/png;base64,"
                        />
                      </template>
                      <vs-button
                        @click.prevent.stop="removeApp(index)"
                        color="danger"
                        icon-pack="feather"
                        icon="icon-trash"
                        type="border"
                      />
                    </vs-list-item>
                    <vs-button
                      @click="$refs.fbPopup.open()"
                      color="primary"
                      icon-pack="feather"
                      icon="icon-plus"
                      type="border"
                      class="w-full my-base"
                      >Add New Firebase App</vs-button
                    >
                  </vs-list>
                </div>
              </div>
            </div>
          </div>
        </div>
      </vx-card>
    </div>
    <AddFirebasePopup ref="fbPopup" />
  </div>
</template>

<script>
import AddFirebasePopup from "./addFirebasePopup";

export default {
  name: "FirstPage",
  components: {
    AddFirebasePopup,
  },
  computed: {
    listFirebaseApp() {
      return this.$store.state.firebaseAppList;
    },
    isFunctionAvailable() {
      return this.$store.state.isFunctionAvailable;
    },
  },
  data() {
    return {
      firebaseConfig: ``,
      placeholder: `Example:
{
  "apiKey":"AIzaSyAfgMxxxxxxxxxxxxxxxxx530dCnE",
  "authDomain":"xxxxxxx.firebaseapp.com",
  "databaseURL":"https://xxxxx.firebaseio.com",
  "projectId":"xxxxx",
  "storageBucket":"xxxx.appspot.com",
  "messagingSenderId":"xxxxxx",
  "appId":"xxxxxxx"
}`,
      config: {},
      msg: "Required",
      reqKey: [
        "apiKey",
        "authDomain",
        "databaseURL",
        "projectId",
        "storageBucket",
        "messagingSenderId",
        "appId",
      ],
    };
  },
  methods: {
    removeApp(index) {
      this.$store.commit("removeFirebaseApp", index);
    },
    async init(app) {
      let initFunction = app.functions().httpsCallable("init");
      await initFunction({
        // default 
        email: 'admin@admin.com',
        password: '123456',
      }).then(console.log).catch(console.log);
      this.$router.push('/dashboard');
    },
    openApp(app) {
      this.$store.commit("setFirebaseApp", app);
      if (app.isInitialized) {
        // check if user exists
        let apptemp = this.$app(app.projectId);
        let user = apptemp.auth().currentUser;
        if (!user) {
          this.$router.push("/pages/login");
        } else {
          this.$router.push("/dashboard");
        }
      } else {
        // try to read previous settings first before init
        let tempapp = this.$app(app.projectId);
        let doc = tempapp
          .firestore()
          .collection("iris_global")
          .doc("iris_settings");
        doc
          .get()
          .then((snapshot) => {
            if (!snapshot.exists || !snapshot.data().initialized) {
              // check for init function for a call
              if (this.isFunctionAvailable) {
                this.init(tempapp);
              } else this.$router.push("/init");
            } else {
              this.$router.push("/dashboard");
            }
          })
          .catch((e) => {
            // error no document or not initialized
            console.log(e);
            if (this.isFunctionAvailable) {
              this.init(tempapp);
            } else this.$router.push("/init");
          });
      }
    },
  },
  mounted() {
    // get init script from host if available
    this.$http.get(location.origin + "/__/firebase/init.js").then((res) => {
      let config = /(?<config>\{.*?\})/ms.exec(res.data);
      console.log(config);
      config = JSON.parse(config.groups.config);
      // config get
      let item = this.$store.state.firebaseAppList.find(
        (t) => t.projectId == config.projectId
      );
      if (item) {
        this.$vs.notify({
          position: "bottom-center",
          time: 2500,
          title: "Already Initialized!",
          iconPack: "feather",
          icon: "icon-alert-circle",
          color: "warning",
        });
      } else {
        this.$store.commit("addFirebaseApp", config);
      }
      this.$firebase.initializeApp(config, config.projectId);
      this.$store.commit("setFirebaseApp", config);
      // try to read previous settings first before init
      let tempapp = this.$app(config.projectId);
      let doc = tempapp
        .firestore()
        .collection("iris_global")
        .doc("iris_settings");
      doc
        .get()
        .then((snapshot) => {
          if (!snapshot.data().initialized) {
            if (this.isFunctionAvailable) {
              this.init(tempapp);
            } else this.$router.push("/init");
          } else {
            this.$router.push("/dashboard");
          }
        })
        .catch((e) => {
          // error no document or not initialized
          console.log(e);
          if (this.isFunctionAvailable) {
              this.init(tempapp);
            } else this.$router.push("/init");
        });
    });
  },
};
</script>

